@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    max-width: 100%;
}

.container {
    background-image: url(/weather-app/static/media/weather-background.f3a66f8b.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.nav-container {
    grid-column: 1/2;
    grid-row: 1/2;
}

.favorites-container {
    grid-column: 1/2;
    grid-row: 2/3
}

.weather-container {
    grid-column: 1/2;
    grid-row: 2/3;
}

.contact-me-container {
    grid-column: 1/2;
    grid-row: 3/4;
}

.footer-container {
    grid-column: 1/2;
    grid-row: 3/4
}
.nav-container {
    height: 5vh;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    background-color: rgba(255, 255, 255, 0.25);
    border-bottom: 1px solid black;
}

.title {
    grid-column: 1/2;
    display: flex;
    justify-self: center;
    align-self: center;
}

.title-font {
    color: black;
    font-weight: 500;
}

.favorites-icon {
    grid-column: 3/4;
    display: flex;
    justify-self: center;
    align-self: center;
}

.nav-links {
    text-decoration: none;
    color: black;
}

.full-fav-icon {
    margin-top: 20%;
    height: 18px;
    width: 18px;
}

.full-fav-icon:hover {
    cursor: pointer;
}

@media screen and (min-width: 768px) {
    .title {
        font-size: 22.5px;
    }
    .full-fav-icon {
        height: 26px;
        width: 26px;
    }
}

@media screen and (min-width: 992px) {
    .title {
        font-size: 22.5px;
    }
    .full-fav-icon {
        height: 25px;
        width: 25px;
    }
}
.current-weather-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr 3fr 1fr;
    height: 95vh;
    max-width: 100%;
}

.search-container {
    grid-column: 1/5;
    grid-row: 1/2;
    align-self: center;
    justify-self: center;
}

.location-form {
    border: none;
    border: 1px solid black;
    border-radius: 15px;
    font-size: 12.5px;
    padding: 7px;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.25);
}

.location-form::-webkit-input-placeholder {
    color: black;
    text-align: center;
}

.location-form:-ms-input-placeholder {
    color: black;
    text-align: center;
}

.location-form::placeholder {
    color: black;
    text-align: center;
}

.location-form:focus::-webkit-input-placeholder {
    color: transparent;
}

.location-form:focus:-ms-input-placeholder {
    color: transparent;
}

.location-form:focus::placeholder {
    color: transparent;
}

.location-form:focus {
    outline: none;
}

.location-button {
    background-color: rgba(255, 255, 255, 0.25);
    color: black;
    padding: 7px 14.5px;
    margin-left: 5px;
    border: 1px solid black;
    cursor: pointer;
    font-size: 12.5px;
    border-radius: 15px;
}

.location-icon {
    grid-column: 4/5;
    grid-row: 1/2;
    align-self: center;
    justify-self: center;
    margin-left: 10px;
}

.location-icon:hover {
    cursor: pointer;
}

.current-weather-info-container {
    grid-column: 1/5;
    grid-row: 2/3;
    display: grid;
    grid-template-rows: 1fr 1fr 2fr;
    grid-template-columns: repeat(2, 1fr);
    background-color: rgba(255, 255, 255, 0.25);
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
}

.city-name {
    display: flex;
    align-self: center;
    justify-content: center;
    grid-column: 1/3;
    grid-row: 1/2;
    font-size: 25px;
}

.current-temp {
    display: flex;
    align-self: center;
    justify-content: center;
    grid-column: 1/3;
    grid-row: 2/3;
    font-size: 30px;
}

.current-weather-icon {
    display: flex;
    align-self: center;
    justify-self: center;
    grid-column: 1/2;
    grid-row: 3/4;
    height: 150px;
    width: 150px;
}

.first-current-weather-section {
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-self: center;
    grid-column: 2/3;
    grid-row: 3/4;
    font-size: 20px;
    line-height: 1.5;
}

.second-current-weather-section {
    grid-column: 1/5;
    grid-row: 3/4;
    display: flex;
    align-self: center;
    justify-content: space-evenly;
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
    background-color: rgba(255, 255, 255, 0.25);
}

.moon-info-container {
    height: 25vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
    background-color: rgba(255, 255, 255, 0.25);
}

.moon-info-title {
    grid-column: 1/3;
    grid-row: 1/2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.moon-phase {
    grid-column: 1/3;
    grid-row: 2/3;
    align-items: center;
}

.moon-phase-info-container {
    grid-column: 1/3;
    grid-row: 2/3;
    display: flex;
    justify-content: space-between;
    margin: 0 5%;
    align-items: center;
}

.moon-img {
    height: 60px;
    width: 60px;
    margin-left: 12.5%;
}

.moonrise-moonset-section {
    grid-column: 1/3;
    grid-row: 3/4;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
}

.weather-forecast-container {
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: space-evenly;
    grid-template-rows: 1fr repeat(4, 1.5fr);
}

.forecast-weather-icons {
    height: 50px;
    width: 50px;
}

.forecast-sunrise-icon {
    display: none;
}

.forecast-sunset-icon {
    display: none;
}

.day-one-forecast {
    grid-row: 2/3;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
    background-color: rgba(255, 255, 255, 0.25);
}

.day-two-forecast {
    grid-row: 3/4;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
    background-color: rgba(255, 255, 255, 0.25);
}

.day-three-forecast {
    grid-row: 4/5;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
    background-color: rgba(255, 255, 255, 0.25);
}

.day-four-forecast {
    grid-row: 5/6;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
    background-color: rgba(255, 255, 255, 0.25);
}

.day-five-forecast {
    grid-row: 6/7;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid black;
    border-radius: 25px;
    margin: 0 20px;
    padding: 10px 5px;
    background-color: rgba(255, 255, 255, 0.25);
}

.contact-me-link {
    display: none;
}

